import { filterstyle, title } from "./styles.module.css";

import actions from "../../redux/contacts/contacts-action";
import { connect } from "react-redux";

function FindInput({ onFilterChange, filter }) {
  return (
    <label className={title}>
      Find contacts by name:
      <input
        className={filterstyle}
        // autocomplete="off"
        type="text"
        name="filter"
        value={filter}
        pattern="^[a-zA-ZÐ°-ÑÐ-Ð¯]+(([' -][a-zA-ZÐ°-ÑÐ-Ð¯ ])?[a-zA-ZÐ°-ÑÐ-Ð¯]*)*$"
        title="Ð˜Ð¼Ñ Ð¼Ð¾Ð¶ÐµÑ‚ ÑÐ¾ÑÑ‚Ð¾ÑÑ‚ÑŒ Ñ‚Ð¾Ð»ÑŒÐºÐ¾ Ð¸Ð· Ð±ÑƒÐºÐ², Ð°Ð¿Ð¾ÑÑ‚Ñ€Ð¾Ñ„Ð°, Ñ‚Ð¸Ñ€Ðµ Ð¸ Ð¿Ñ€Ð¾Ð±ÐµÐ»Ð¾Ð². ÐÐ°Ð¿Ñ€Ð¸Ð¼ÐµÑ€ Adrian, Jacob Mercer, Charles de Batz de Castelmore d'Artagnan Ð¸ Ñ‚. Ð¿."
        required
        onChange={onFilterChange}
      />
    </label>
  );
}

const mapStateToProps = ({ contacts: { filter } }) => {
  return {
    filter,
  };
};
const mapDispatchToProps = (dispatch) => {
  return {
    onFilterChange: (event) =>
      dispatch(actions.filterChange(event.currentTarget.value)),
  };
};
export default connect(mapStateToProps, mapDispatchToProps)(FindInput);
